.buttonDivUpdate {
  border-top: 10px double white;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  padding-top: 1rem;
  margin-top: 5rem;
}

.buttonUpdate {
    position: absolute;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    background-color: #5369cc;
    color: #ffffff;
    font-size: 20px;
    font-weight: 600;
    transition: background-color .5s;
  }
  
  .buttonUpdate:hover {
    cursor: pointer;
    background-color: #1d63e6;
  }