.SearchPage {
  background-image: url('./forside.jpg');
  height: 55.8rem;
}

.PageHeader {
  border-bottom: 3px solid white;
}

.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* background-image: url('./concrete-texture-bg.jpg'); */
  background-color: #5ea9f0dc;
  background-size: cover;
  background-repeat: no-repeat;
  height: 9rem;
  border-bottom: 3px solid white;
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  cursor: pointer;
  width: 4.33rem;
  border-bottom: 1px solid #fff;
  padding: 0 2.58rem .33rem 2.58rem;
  line-height: 1.13;
  text-align: center;
  font-weight: 600;
  font-size: .83rem;
  transition: color .25s;
}

.SearchBar-sort-options li:hover {
  color: #d4cfcf;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  border-bottom: 1px solid #537dcc;
  color: #537dcc;
}

.buttonSearch {
  position: absolute;
  margin-top: 0.5rem;
  margin-left: 6.5rem;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  background-color: #5369cc;
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
  transition: background-color .5s;
}

.buttonSearch:hover {
  cursor: pointer;
  background-color: #1d63e6;
}

.SearchHeader {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #282c34;
  padding-bottom: 2rem;
}

.searchInfo {
  position: absolute;
  left: 61rem;
  top: 7.3rem;
  opacity: 1;
}

.ToolTipList {
  margin-top: 0.3rem;
}

.ToolTipList li {
  margin-top: 0.3rem;
}