
.ImpactSection {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ImpactHeader {
    margin-top: 2rem;
    border-top: 3px solid white;
    border-bottom: 3px solid white;
    background-color: rgb(29, 109, 201);
    width: 100%;
}

.ImpactHeader h1{
    font-size: 40px;
    background-color: rgb(29, 109, 201);
}

.emissionUpdate {
    position: relative;
    top: 1.5rem;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    background-color: #5369cc;
    color: #ffffff;
    font-size: 20px;
    font-weight: 600;
    transition: background-color .5s;
    width: 160px;
}
  
.emissionUpdate:hover {
    cursor: pointer;
    background-color: #1d63e6;
}

.emissionContainer {
    position: relative;
    display:flex;
    justify-content: center;
    top: 3.5rem;
    left: 3rem;
}

.emissionContainer li {
    list-style-type: none;
}

.emissionTitle {
    font-size: 20px;
    font-weight: 450;
    margin-bottom: 0.5rem;
}

.climateEmission {
    margin-right: 3rem;
}