
.ResultHeader {
    margin-top: 2rem;
    border-top: 3px solid white;
    border-bottom: 3px solid white;
    background-color: rgb(29, 109, 201);
    font-size: 40px;
}

.ResultContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.aResult {
    margin-top: 1rem;
    width: 15%;
}

.aResult p{
    font-size: 30px;
}

.aResult li {
    margin-top: 0.5rem;
    font-size: 20px;
    list-style-type: none;
}

.SumContainer {
    display: flex;
    justify-content: center;
}

.SumContainer p{
    font-size: 30px;
}

.SumResult {
    margin-top: 1rem;
}

.SumResult li {
    margin-top: 0.5rem;
    font-size: 20px;
    list-style-type: none;
}

.ChartContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 1rem;
}

.BarContainer {
    margin-left: 2rem;
    width: 500px;
    height: 500px;
    border: 5px groove rgb(32, 102, 253);
    border-radius: 0.5rem;
}